@import "../node_modules/compass-mixins/lib/compass/css3";

html, body{height:100%;width:100%;}
*{box-sizing:border-box;}
body{
background-image: url(data:image/png;base64,);
  // font-family:TimesNewRoman;  
  // background-image: url("thumb.jpg");
  // background-color: 	#ffffff ;//#f0f5f5 //#cce6ff;
  position:absolute;
  @include box-shadow(inset black 0px 20px 20px);
}
h2{
  font-size:10px;
  font-weight:bold;
  margin-bottom: 2px;
}
p{
  font-size:8px;
  line-height:12px;
  margin:0px;
}
$partHeight:100px;
$verticalPadding:8px;
$horizontalPadding:5px;
$prefixes:"-webkit-" "-moz-" "-ms-" "-o-" "";

.paper{
  height:$partHeight*3;
  width:210px;
  margin:10px auto;
  zoom: 150%;

  @each $prefix in $prefixes{
    #{$prefix}perspective:800px;
    #{$prefix}backface-visibility:hidden;
  }

  .part1{
    position:relative;
    z-index:2;
    background-color:white;
    height:$partHeight - $verticalPadding;
    padding:$verticalPadding $horizontalPadding 0px $horizontalPadding;
    @include transition(all .6s linear);
    @include box-shadow(inset rgba(0,0,0,.3) 0px 0px 100px, rgba(0,0,0,.4) 0px -4px 10px);
  }
  .part2{
    background-color:white;
    height:$partHeight;
    padding:0px $horizontalPadding 0px $horizontalPadding;
    @include transition(all .6s linear);
    @include box-shadow(inset rgba(0,0,0,.2) 0px 0px 100px, black 0px 0px 10px);
  }
  .part3{
    background-color:white;
    height:$partHeight - $verticalPadding;
    padding:0px $horizontalPadding $verticalPadding $horizontalPadding;
    @include transition(all .6s linear);
    @include box-shadow(inset rgba(0,0,0,.3) 0px 0px 100px, rgba(0,0,0,.4) 0px 4px 10px);
  }

  &:hover{
    .part1{
      @include transform(rotateX(-30deg) translateY(0.65em) scale(0.98));
      @include transform-origin(50%, 100%);
      @include box-shadow(inset rgba(0,0,0,.5) 0px 0px 100px, rgba(0,0,0,.1) 0px -20px 10px);
    }
    .part2{
      @include transform(rotateX(30deg) scale(1));
      @include transform-origin(50%, 50%);
      @include box-shadow(inset rgba(0,0,0,.4) 0px 80px 100px);
    }
    .part3{
      @include transform(rotateX(-30deg) translateY(-0.45em) scale(1.026));
      @include transform-origin(50%, 0%);
      @include box-shadow(inset rgba(0,0,0,.4) 0px 80px 100px);
    }
  }
}
